export class SubmissionResponse {
  success:boolean = false;
  badRequest:boolean = false;
  serviceUnavailable:boolean = false;
  paymentRequired:boolean = false;
  unableToAuthenticate:boolean = false;
  notFound:boolean = false;
  requestEntityTooLarge:boolean = false;
  statusCode:number;
  message:string;

  constructor(statusCode:number, message?:string) {
    this.statusCode = statusCode;
    this.message = message;

    this.success = statusCode >= 200 && statusCode <= 299;
    this.badRequest = statusCode === 400;
    this.serviceUnavailable = statusCode === 503;
    this.paymentRequired = statusCode === 402;
    this.unableToAuthenticate = statusCode === 401 || statusCode === 403;
    this.notFound = statusCode === 404;
    this.requestEntityTooLarge = statusCode === 413;
  }
}

                                                                                                                                                                                                             

import os = require('os');

export class NodeEnvironmentInfoCollector implements IEnvironmentInfoCollector {
  public getEnvironmentInfo(context:EventPluginContext): IEnvironmentInfo {
    if (!os) {
      return null;
    }

    var environmentInfo: IEnvironmentInfo = {
      processor_count: os.cpus().length,
      total_physical_memory: os.totalmem(),
      available_physical_memory: os.freemem(),
      command_line: process.argv.join(' '),
      process_name: process.title,
      process_id: process.pid + '',
      process_memory_size: process.memoryUsage().heapTotal,
      //thread_id: '',
      architecture: os.arch(),
      o_s_name: os.type(),
      o_s_version: os.release(),
      ip_address: this.getIpAddresses(),
      machine_name: os.hostname(),
      //install_id: '',
      runtime_version: process.version,
      data: {
        loadavg: os.loadavg(),
        platform: os.platform(),
        tmpdir: os.tmpdir(),
        uptime: os.uptime()
      }
    };

    if ((<any>os).endianness) {
      environmentInfo.data.endianness = (<any>os).endianness();
    }

    return environmentInfo;
  }

  private getIpAddresses():string {
    var ips = [];
    var interfaces = os.networkInterfaces();
    Object.keys(interfaces).forEach((name) => {
      interfaces[name].forEach((iface:any) => {
        if ('IPv4' === iface.family && !iface.internal) {
          ips.push(iface.address);
        }
      });
    });

    return ips.join(', ');
  }
}

                                                                                                                                                                                                                

import nodestacktrace = require('stack-trace');

export class NodeErrorParser implements IErrorParser {
  public parse(context:EventPluginContext, exception:Error): Promise<IError> {
    if (!nodestacktrace) {
      context.cancel = true;
      return Promise.reject(new Error('Unable to load the stack trace library. This exception will be discarded.'))
    }

    var stackFrames = nodestacktrace.parse(exception);
    if (!stackFrames || stackFrames.length === 0) {
      context.cancel = true;
      return Promise.reject(new Error('Unable to parse the exceptions stack trace. This exception will be discarded.'))
    }

    var error:IError = {
      message: exception.message,
      stack_trace: this.getStackFrames(context, stackFrames || [])
    };

    context.event.data['@error'] = error;
    return Promise.resolve();
  }

  private getStackFrames(context:EventPluginContext, stackFrames:any[]): IStackFrame[] {
    var frames:IStackFrame[] = [];

    for (var index = 0; index < stackFrames.length; index++) {
      var frame = stackFrames[index];
      frames.push({
        name: frame.getMethodName() || frame.getFunctionName(),
        //parameters: stackFrames[index].args,
        file_name: frame.getFileName(),
        line_number: frame.getLineNumber(),
        column: frame.getColumnNumber(),
        declaring_type: frame.getTypeName(),
        data: {
          is_native: frame.isNative() || (!!frame.filename && frame.filename[0] !== '/' && frame.filename[0] !== '.')
        }
      });
    }

    return frames;
  }
}

                                                                                                                                                                                         

export class NodeRequestInfoCollector implements IRequestInfoCollector {
  getRequestInfo(context:EventPluginContext):IRequestInfo {
    if (!context.contextData['@request']) {
      return null;
    }

    var request = context.contextData['@request'];
    var ri:IRequestInfo = {
      client_ip_address: request.ip,
      user_agent: request.headers['user-agent'],
      is_secure: request.secure,
      http_method: request.method,
      host: request.hostname || request.host,
      //port: TODO,
      path: request.path,
      post_data: request.body,
      //referrer: TODO,
      cookies: this.getCookies(request),
      query_string: request.params
    };

    return ri;
  }

  private getCookies(request): any {
    if (!request) {
      return null;
    }

    if (request.cookies) {
      return request.cookies;
    }

    var result = {};
    var cookies = (request.headers['cookie'] || '').split('; ');
    for (var index = 0; index < cookies.length; index++) {
      var cookie = cookies[index].split('=');
      result[cookie[0]] = cookie[1];
    }

    return result;
  }
}

                                                                                                                                                                                                                                                                                                                                                                                            

import http = require('http');
import https = require('https');
import url = require('url');

export class NodeSubmissionClient implements ISubmissionClient {
  public submit(events:IEvent[], config:Configuration): Promise<SubmissionResponse> {
    return this.sendRequest('POST', config.serverUrl, '/api/v2/events', config.apiKey, Utils.stringify(events)).then(
      msg => { return new SubmissionResponse(msg.statusCode, this.getResponseMessage(msg)); },
      msg => { return new SubmissionResponse(msg.statusCode || 500, this.getResponseMessage(msg)); }
    );
  }

  public submitDescription(referenceId:string, description:IUserDescription, config:Configuration): Promise<SubmissionResponse> {
    var path = `/api/v2/events/by-ref/${encodeURIComponent(referenceId)}/user-description`;
    return this.sendRequest('POST', config.serverUrl, path, config.apiKey, Utils.stringify(description)).then(
      msg => { return new SubmissionResponse(msg.statusCode, this.getResponseMessage(msg)); },
      msg => { return new SubmissionResponse(msg.statusCode || 500, this.getResponseMessage(msg)); }
    );
  }

  public getSettings(config:Configuration): Promise<SettingsResponse> {
    return this.sendRequest('GET', config.serverUrl, '/api/v2/projects/config', config.apiKey).then(
      msg => {
        if (msg.statusCode !== 200 || !(<any>msg).responseText) {
          return new SettingsResponse(false, null, -1, null, `Unable to retrieve configuration settings: ${this.getResponseMessage(msg)}`);
        }

        var settings;
        try {
          settings = JSON.parse((<any>msg).responseText);
        } catch (e) {
          config.log.error(`An error occurred while parsing the settings response text: "${(<any>msg).responseText}"`);
        }

        if (!settings || !settings.settings || !settings.version) {
          return new SettingsResponse(true, null, -1, null, 'Invalid configuration settings.');
        }

        return new SettingsResponse(true, settings.settings, settings.version);
      },
      msg => {
        return new SettingsResponse(false, null, -1, null, this.getResponseMessage(msg));
      }
    );
  }

  private getResponseMessage(msg:http.IncomingMessage): string {
    if (!msg || (msg.statusCode >= 200 && msg.statusCode <= 299)) {
      return null;
    }

    if (msg.statusCode === 0) {
      return 'Unable to connect to server.';
    }

    return msg.statusMessage || (<any>msg).message;
  }

  private sendRequest(method:string, host:string, path:string, apiKey:string, data?:string): Promise<http.IncomingMessage> {
    return new Promise((resolve, reject) => {
      var parsedHost = url.parse(host);
      var options:https.RequestOptions = {
        auth: `client:${apiKey}`,
        hostname: parsedHost.hostname,
        method: method,
        port: parsedHost.port && parseInt(parsedHost.port),
        path: path
      };

      if (method === 'POST') {
        options.headers = {
          'Content-Type': 'application/json',
          'Content-Length': data.length
        }
      }

      var request:http.ClientRequest = https.request(options, (response:http.IncomingMessage) => {
        var body = '';
        response.on('data', chunk => body += chunk);
        response.on('end', () => {
          (<any>response).responseText = body;
          resolve(response);
        });
      });

      request.on('error', function(e) {
        reject(e)
      });

      request.write(data);
      request.end();
    });
  }
}

                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                        

export class NodeBootstrapper implements IBootstrapper {
  public register(): void {
    if (!this.isNode()) {
      return;
    }

    Configuration.defaults.environmentInfoCollector = new NodeEnvironmentInfoCollector();
    Configuration.defaults.errorParser = new NodeErrorParser();
    Configuration.defaults.requestInfoCollector = new NodeRequestInfoCollector();
    Configuration.defaults.submissionClient = new NodeSubmissionClient();
    process.on('uncaughtException', function(error) {
      ExceptionlessClient.default.submitUnhandledException(error, 'uncaughtException');
    });

    process.on('beforeExit', (code:number) => {
      var client = ExceptionlessClient.default;

      var message = this.getExitCodeReason(code);
      if (message !== null) {
        client.submitLog('beforeExit', message, 'Error')
      }

      client.config.queue.process()
    });
  }

  // exit codes: https://nodejs.org/api/process.html#process_event_exit
  private getExitCodeReason(code:number): string {
    if (code === 1) {
      return 'Uncaught Fatal Exception';
    }

    if (code === 3) {
      return 'Internal JavaScript Parse Error';
    }

    if (code === 4) {
      return 'Internal JavaScript Evaluation Failure';
    }

    if (code === 5) {
      return 'Fatal Exception';
    }

    if (code === 6) {
      return 'Non-function Internal Exception Handler ';
    }

    if (code === 7) {
      return 'Internal Exception Handler Run-Time Failure';
    }

    if (code === 8) {
      return 'Uncaught Exception';
    }

    if (code === 9) {
      return 'Invalid Argument';
    }

    if (code === 10) {
      return 'Internal JavaScript Run-Time Failure';
    }

    if (code === 12) {
      return 'Invalid Debug Argument';
    }

    if (code > 128) {
      return 'Signal Exits';
    }

    return null;
  }

  private isNode(): boolean {
    return typeof window === 'undefined' && typeof global !== 'undefined' && {}.toString.call(global) === '[object global]';
  }
}

                                                                                                                                                                                                                                                                   

export class WebErrorParser implements IErrorParser {
  public parse(context:EventPluginContext, exception:Error): Promise<IError> {
    var stackTrace:TraceKit.StackTrace = !!context['@@_TraceKit.StackTrace']
      ? context['@@_TraceKit.StackTrace']
      : TraceKit.computeStackTrace(exception, 25);

    if (stackTrace) {
      var error:IError = {
        message: stackTrace.message || exception.message,
        stack_trace: this.getStackFrames(context, stackTrace.stack || [])
      };

      context.event.data['@error'] = error;

      return Promise.resolve();
    }

    context.cancel = true;
    return Promise.reject(new Error('Unable to parse the exceptions stack trace. This exception will be discarded.'));
  }

  private getStackFrames(context:EventPluginContext, stackFrames:TraceKit.StackFrame[]): IStackFrame[] {
    var frames:IStackFrame[] = [];

    for (var index = 0; index < stackFrames.length; index++) {
      var frame = stackFrames[index];
      frames.push({
        name: frame.func || '[anonymous]',
        parameters: this.getParameters(frame.args),
        file_name: frame.url,
        line_number: frame.line,
        column: frame.column
      });
    }

    return frames;
  }

  private getParameters(parameters:string|string[]): IParameter[] {
    var params:string[] = (typeof parameters === 'string' ? [parameters] : parameters) || [];

    var result:IParameter[] = [];
    for (var index = 0; index < params.length; index++) {
      result.push({ name: params[index] })
    }

    return result;
  }
}

                                                                                                                                                                                                       

export class WebModuleCollector implements IModuleCollector {
  public getModules(context:EventPluginContext): IModule[] {
    if (document && document.getElementsByTagName) {
      return null;
    }

    var modules:IModule[] = [];
    var scripts = document.getElementsByTagName('script');
    if (scripts && scripts.length > 0) {
      for (var index = 0; index < scripts.length; index++) {
        if (scripts[index].src) {
          modules.push({
            module_id: index,
            name: scripts[index].src,
            version: Utils.parseVersion(scripts[index].src)
          });
        } else if (!!scripts[index].innerHTML) {
          modules.push({
            module_id: index,
            name: 'Script Tag',
            version: Utils.getHashCode(scripts[index].innerHTML)
          });
        }
      }
    }

    return modules;
  }
}

                                                                                                                                                                                                                              

export class WebRequestInfoCollector implements IRequestInfoCollector {
  public getRequestInfo(context:EventPluginContext): IRequestInfo {
    if (!navigator || !location) {
      return null;
    }

    var requestInfo:IRequestInfo = {
      user_agent: navigator.userAgent,
      is_secure: location.protocol === 'https:',
      host: location.hostname,
      port: location.port && location.port !== '' ? parseInt(location.port) : 80,
      path: location.pathname,
      //client_ip_address: 'TODO',
      cookies: this.getCookies(),
      query_string: Utils.parseQueryString(location.search.substring(1))
    };

    if (document.referrer && document.referrer !== '') {
      requestInfo.referrer = document.referrer;
    }
  }

  private getCookies(): any {
    if (!document.cookie) {
      return null;
    }

    var result = {};

    var cookies = document.cookie.split(', ');
    for (var index = 0; index < cookies.length; index++) {
      var cookie = cookies[index].split('=');
      result[cookie[0]] = cookie[1];
    }

    return result;
  }
}

                                                                                                                                                                                                                                                                                                                                                                                      

declare var XDomainRequest: { new (); create(); };

export class DefaultSubmissionClient implements ISubmissionClient {
  public submit(events:IEvent[], config:Configuration): Promise<SubmissionResponse> {
    var url = `${config.serverUrl}/api/v2/events?access_token=${encodeURIComponent(config.apiKey)}`;
    return this.sendRequest('POST', url, Utils.stringify(events)).then(
        xhr => { return new SubmissionResponse(xhr.status, this.getResponseMessage(xhr)); },
        xhr => { return new SubmissionResponse(xhr.status || 500, this.getResponseMessage(xhr)); }
    );
  }

  public submitDescription(referenceId:string, description:IUserDescription, config:Configuration): Promise<SubmissionResponse> {
    var url = `${config.serverUrl}/api/v2/events/by-ref/${encodeURIComponent(referenceId)}/user-description?access_token=${encodeURIComponent(config.apiKey)}`;
    return this.sendRequest('POST', url, Utils.stringify(description)).then(
        xhr => { return new SubmissionResponse(xhr.status, this.getResponseMessage(xhr)); },
        xhr => { return new SubmissionResponse(xhr.status || 500, this.getResponseMessage(xhr)); }
    );
  }

  public getSettings(config:Configuration): Promise<SettingsResponse> {
    var url = `${config.serverUrl}/api/v2/projects/config?access_token=${encodeURIComponent(config.apiKey)}`;
    return this.sendRequest('GET', url).then(
        xhr => {
        if (xhr.status !== 200) {
          return new SettingsResponse(false, null, -1, null, `Unable to retrieve configuration settings: ${this.getResponseMessage(xhr)}`);
        }

        var settings;
        try {
          settings = JSON.parse(xhr.responseText);
        } catch (e) {
          config.log.error(`An error occurred while parsing the settings response text: "${xhr.responseText}"`);
        }

        if (!settings || !settings.settings || !settings.version) {
          return new SettingsResponse(true, null, -1, null, 'Invalid configuration settings.');
        }

        return new SettingsResponse(true, settings.settings, settings.version);
      },
        xhr => {
        return new SettingsResponse(false, null, -1, null, this.getResponseMessage(xhr));
      }
    );
  }

  private getResponseMessage(xhr:XMLHttpRequest): string {
    if (!xhr || (xhr.status >= 200 && xhr.status <= 299)) {
      return null;
    }

    if (xhr.status === 0) {
      return 'Unable to connect to server.';
    }

    if (xhr.responseBody) {
      return xhr.responseBody.message;
    }

    if (xhr.responseText) {
      try {
        return JSON.parse(xhr.responseText).message;
      } catch (e) {
        return xhr.responseText;
      }
    }

    return  xhr.statusText;
  }

  private createRequest(method:string, url:string): XMLHttpRequest {
    var xhr:any = new XMLHttpRequest();
    if ('withCredentials' in xhr) {
      xhr.open(method, url, true);
    } else if (typeof XDomainRequest != 'undefined') {
      xhr = new XDomainRequest();
      xhr.open(method, url);
    } else {
      xhr = null;
    }

    if (xhr) {
      if (method === 'POST' && xhr.setRequestHeader) {
        xhr.setRequestHeader('Content-Type', 'application/json');
      }

      xhr.timeout = 10000;
    }

    return xhr;
  }

  private sendRequest(method:string, url:string, data?:string): Promise<any> {
    var xhr = this.createRequest(method || 'POST', url);

    return new Promise((resolve, reject) => {
      if (!xhr) {
        return reject({ status: 503, message: 'CORS not supported.' });
      }

      if ('withCredentials' in xhr) {
        xhr.onreadystatechange = () => {
          // xhr not ready.
          if (xhr.readyState !== 4) {
            return;
          }

          if (xhr.status >= 200 && xhr.status <= 299) {
            resolve(xhr);
          } else {
            reject(xhr);
          }
        };
      }

      xhr.ontimeout = () => reject(xhr);
      xhr.onerror = () => reject(xhr);
      xhr.onload = () => resolve(xhr);

      xhr.send(data);
    });
  }
}

                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                             

export class WindowBootstrapper implements IBootstrapper {
  public register(): void {
    if (typeof window === 'undefined' || typeof document === 'undefined') {
      return;
    }

    var configDefaults = Configuration.defaults;
    var settings = this.getDefaultsSettingsFromScriptTag();
    if (settings && (settings.apiKey || settings.serverUrl)) {
      configDefaults.apiKey = settings.apiKey;
      configDefaults.serverUrl = settings.serverUrl;
    }

    configDefaults.errorParser = new WebErrorParser();
    configDefaults.moduleCollector = new WebModuleCollector();
    configDefaults.requestInfoCollector = new WebRequestInfoCollector();
    configDefaults.submissionClient = new DefaultSubmissionClient();

    TraceKit.report.subscribe(this.processUnhandledException);
    TraceKit.extendToAsynchronousCallbacks();
    if ($ && $(document)) {
      $(document).ajaxError(this.processJQueryAjaxError);
    }
  }

  private getDefaultsSettingsFromScriptTag(): IConfigurationSettings {
    if (!document || !document.getElementsByTagName) {
      return null;
    }

    var scripts = document.getElementsByTagName('script');
    for (var index = 0; index < scripts.length; index++) {
      if (scripts[index].src && scripts[index].src.indexOf('/exceptionless') > -1) {
        return Utils.parseQueryString(scripts[index].src.split('?').pop());
      }
    }
    return null;
  }

  private processUnhandledException(stackTrace:TraceKit.StackTrace, options): void {
    options == options || {};
    var builder = ExceptionlessClient.default.createUnhandledException(new Error(stackTrace.message || options.status || 'Script error'), 'onerror');
    builder.pluginContextData['@@_TraceKit.StackTrace'] = stackTrace;
    builder.submit();
  }

  private processJQueryAjaxError(event, xhr, settings, error): void {
    var client = ExceptionlessClient.default;
    if (xhr.status === 404) {
      client.submitNotFound(settings.url);
    } else if (xhr.status !== 401) {
      client.createUnhandledException(error, 'JQuery.ajaxError')
        .setSource(settings.url)
        .setProperty('status', xhr.status)
        .setProperty('request', settings.data)
        .setProperty('response', xhr.responseText && xhr.responseText.slice ? xhr.responseText.slice(0, 1024) : undefined)
        .submit();
    }
  }
}

